 
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-container-check {
    position: absolute;
    left: 12px;
  }
  
  
  .Icon-container-delete {
    position: absolute;
    top: -12px;
    right: 0;
  }
  

  .Icon-svg{
    width: 24px;
    height: 24px;
  }

  .Icon-container-check:hover{
    cursor: pointer;
  }

  .Icon-container-delete:hover {
    cursor: pointer;
  }

  .Icon-container-check:hover .Icon.svg {
    fill: #4caf50;
  }

  .Icon-container-delete:hover .Icon-svg {
    fill: red;
  }


  

  