.CreateTodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    /* position: fixed; */
    /* bottom: 24px; Poner lo comentado para la version mobile xd
    right: 24px; */
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
  
    transform: rotate(0);
    transition: .3s ease;
    margin-top: 2rem;
  }
  
  .CreateTodoButton:hover {
    transform: rotate(224deg);
  }
  @media (max-width: 600px) {
    .CreateTodoButton {
      margin-top: 0;
      position: fixed;
      bottom: 0;
      right: 12px;
    }
  }