.TodoContainer{
    display: flex;
}

.Right{
    flex: 1;
}

.Left{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}


@media (max-width: 600px) {
    .TodoContainer {
      flex-direction: column;
      padding: 0.5rem;
    }
  }